body{
  padding:60px;
}

input{
  width:100%;
  color:green;
  font-size: 1.5rem;
}

* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.card-grid{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden
}
  .card p{
    text-align: center;
    padding: 5px;
  };

  .card img{
    max-height: 100px;
  }



